/* jshint ignore:start */

define('sdg-dash/config/environment', ['ember'], function(Ember) {
  var prefix = 'sdg-dash';
/* jshint ignore:start */

try {
  var metaName = prefix + '/config/environment';
  var rawConfig = Ember['default'].$('meta[name="' + metaName + '"]').attr('content');
  var config = JSON.parse(unescape(rawConfig));

  return { 'default': config };
}
catch(err) {
  throw new Error('Could not read config from meta tag with name "' + metaName + '".');
}

/* jshint ignore:end */

});

if (!runningTests) {
  require("sdg-dash/app")["default"].create({"name":"sdg-dash","version":"0.0.0+202dff5e"});
}

/* jshint ignore:end */
